const toUpperAll = str => {
    // split by " " --
    str = str.trim().split(" ");
    for (var i = 0, x = str.length; i < x; i++) {
        str[i] = str[i][0].toUpperCase() + str[i].substr(1).toLowerCase();
        // split by "-" --
        if(str[i].includes('-')){
            let T = str[i].split("-");
            for (var j = 1, y = T.length; j < y; j++) {
                T[j] = T[j][0].toUpperCase() + T[j].substr(1).toLowerCase();
            }
            str[i] = T.join("-");
        }      
        // [end] split by "-" --
    }
    return str.join(" ");
}
export default toUpperAll;