import React, { useState, useEffect } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import './App.scss';
import toUpperAll from './util/toUpperAll.js';
import csvData from './util/csvData.js';
import arrayUnique from './util/arrayUnique.js';
import logo from './images/logo.png';

import Spinner from './components/Spinner/Spinner';
import TextSearch from './components/TextSearch/TextSearch';
import DoctorsDetails from './components/DoctorsDetails/DoctorsDetails';
import ScrollTopBtn from './components/ScrollTopBtn/ScrollTopBtn';

import doctorsCsv from './data/doctors.csv';

require('bootstrap')

const App = () => {
  const [doctors, setDoctors] = useState(null);
  const [locList, setLocList] = useState(null);
  const [filteredDr, setFilteredDr] = useState([]);
  const [locTag, setLocTag] = useState([]);

  // update filtered doctor list by search text --
  const searchTextHandler = txt => {
    txt = txt.trim().toLowerCase();
    let matchedLoc = [];
    if(txt.length){
      // search for exact match --
      const exactMatch = doctors.filter(item => [item.Location.toLowerCase(), item.Region.toLowerCase()].includes(txt));
      if(exactMatch.length){
        setLocTag([toUpperAll(txt)]);
        setFilteredDr(exactMatch);
      }else{
        // if no exact match, search for matching words --
        const commonWords = (a, b) => {
          // return common words of 2 string --
          a = a.toLowerCase().split(' ');
          b = b.toLowerCase().split(' ');
          return a.filter(value => b.includes(value));
        }

        const filteredDrList = doctors.filter(item => {
          // search matching words in Region --
          const matchedRegionWords = commonWords(txt, item.Region);          
          if(matchedRegionWords.length){
            matchedLoc.push(toUpperAll(item.Region));
          }          
          
          // search matching words in Location --
          const matchedLocationWords = commonWords(txt, item.Location);
          if(matchedLocationWords.length){
            matchedLoc.push(toUpperAll(item.Location));
          }

          const matchedWords = matchedLocationWords.concat(matchedRegionWords);          
          return matchedWords.length > 0;    
        }); 

        setLocTag(arrayUnique(matchedLoc).sort());
        setFilteredDr(filteredDrList);
      }
    }else{
      //if search text is empty, reset to initial state --
      setLocTag([]);
      setFilteredDr(doctors);
    }
  }
  // [end] update filtered doctor list by search text --

  // fetch csv data --
  useEffect(() => {
    csvData(doctorsCsv, data => {
      data.forEach(item => {
        // correct text --
        let region = item['Region'];
        region = region.replace("New Terriroties", "New Territories")
        .replace("Hong Kong Island", "Hong Kong")
        .replace("Hong Kong", "Hong Kong Island");
        item['Region'] = toUpperAll(region);

        let location = item['Location'];
        location = location.replace("hunghom", "Hung Hom")        
        .replace("Hunghom", "Hung Hom")
        .replace("Kenndy Town", "Kennedy Town")
        .replace("Kennedy Twon", "Kennedy Town")
        .replace("MongKok", "Mong Kok")
        .replace("Mongkok", "Mong Kok")
        .replace("Ngan Tau Kok", "Ngau Tau Kok")
        .replace("Prinece Edward", "Prince Edward")
        .replace("Shatin", "Sha Tin");
        item['Location'] = toUpperAll(location);
        // [end] correct text --

        // split services types --
        item['Service Type'] = item['Service Type'].split('/');
        // [end] split services types --
      });
      
      // sort by doctor's name --
      data.sort((a, b) => {         
        if(a['Region'] !== b['Region']){
          //1st sort by Region 
          return a['Region'] < b['Region'] ? -1 : 1;
        }else if(a['Location'] !== b['Location']){
          //2nd sort by Location 
          return a['Location'] < b['Location'] ? -1 : 1;
        }else if(a['Name'] !== b['Name']){
          //3rd sort by name
          return a['Name'] < b['Name'] ? -1 : 1; 
        }else{
          return 0;
        }      
      });
      setDoctors(data);
      
      // get search keyword list --
      //-// get regions --
      const regions = data.map(item => item.Region);
      //-// get locations --
      const locations = data.map(item => item.Location);
      let locs = regions.concat(locations);
      locs = arrayUnique(locs);
      locs.sort();
      setLocList(locs);
      // [end] get search keyword list --            
    });    
  }, []);
  // [end] fetch csv data --

  let pageBody = <Spinner />;
  if(doctors && locList){
    pageBody = <React.Fragment>        
        <TextSearch locList={locList} updated={searchTextHandler} resultNum={filteredDr.length} locTag={locTag} />
        <DoctorsDetails doctors={filteredDr} fullListLength={doctors.length} />
        <ScrollTopBtn/>
      </React.Fragment>
  }
  return <div className="App">
      <header>
        <div className="container">
          <img src={logo} alt="bowtiego" className='logo'/>
        </div>
      </header>
      {pageBody}
    </div>;
}

export default App;
