import React, { useState, useEffect } from 'react';
import styles from './TextSearch.module.scss';
import toUpperAll from '../../util/toUpperAll.js';
const classNames = require('classnames');

const TextSearch = ({ locList, resultNum, updated, locTag }) => {
    const [keywords, setKeywords] = useState('');

    const keywordsHandler = e => {
        setKeywords(e.target.value);
    }
    const resetKeywordsHandler = () => {
        setKeywords('');
    }

    useEffect(() => {
        updated(keywords);
    // eslint-disable-next-line
    }, [keywords]);

    let  locations = null;
    if(keywords.length){
        locations = locList.map(item => {
            return <option value={item} key={item}/>;
        });
    }

    let keywordsTag = null;
    if(locTag.length){
        if(locTag.length === 1 && locTag[0].toLowerCase() === keywords.toLowerCase()){
            // exact match case --
            keywordsTag = <div className={styles.KeywordTag} onClick={resetKeywordsHandler}>{locTag[0]} <i className="fas fa-times"></i></div>
        }else if (locTag.length > 0){
            //multiple matching case --
            keywordsTag = locTag.map(item => {
                return <div className={styles.KeywordTag} onClick={() => setKeywords(item)} key={item}>{item}</div>;
            }); 
        }
    }
    

    return (<section className={styles.TextSearch}>
        <div className={classNames(styles.Container, 'container')}>
            <h1 className={styles.Title}>Search Doctors by Locations</h1>
            <div>
                <input className={styles.SearchInput} type="text" id="search" name="search" 
                    placeholder="Type here to search" list="locList" value={keywords}
                    onChange={keywordsHandler}></input>
                <datalist id="locList">{locations}</datalist>
                <div className={classNames(styles.CloseBtn, {[styles.CloseBtnDisabled]: !keywords.length})} 
                    onClick={resetKeywordsHandler}>
                        <i className="fas fa-times"></i>
                    </div>
            </div>                         
        </div>
        <div className={classNames(styles.KeywordTagContainer, 'container')}>{keywordsTag}</div>
    </section>);
};
export default TextSearch;
