import React, { useState, useEffect } from 'react';
import styles from './DoctorsDetails.module.scss';
import TransitionGroup from "react-transition-group/TransitionGroup";
import CSSTransition from "react-transition-group/CSSTransition";
import DoctorDetails from './DoctorDetails/DoctorDetails';
import LoadMoreBtn from './LoadMoreBtn/LoadMoreBtn';
const pluralize = require('pluralize')

const feedPerBatch = 10;

const DoctorsDetails = ({doctors, fullListLength}) => {
    const [feedNum, setFeedNum] = useState(feedPerBatch);

    const loadFeedsHandler = () =>{
        setFeedNum(feedNum + feedPerBatch);
    }

    // reset num of feed when doctor list is updated --
    useEffect(() => {
        setFeedNum(feedPerBatch);
    }, [doctors]);

    let recordsCountTxt = doctors.length < fullListLength ?
        `${pluralize('record', doctors.length, true)} found in this search` : 
        `${pluralize('record', fullListLength, true)} in total`;

    let listBody = null;
    if (doctors.length){
        listBody = doctors
        .map((item, index) => {
            if (index >= feedNum){                
                return null;
            }
            const _key = `${index}-${item['Doctor ID']}-${item['Name']}-${item['Service Type']}-${item['Address1']}-${item['Price']}-${item['Region']}-${item['Location']}`;
            return (<CSSTransition key={_key} classNames="fade" timeout={500}>
                    <DoctorDetails details={item}/>
                </CSSTransition>);
        });
    }

    let loadMoreBtn = null;
    if (doctors.length && doctors.length > feedNum){
        loadMoreBtn = <LoadMoreBtn loadMore={loadFeedsHandler} />;
    }
    return (
        <section className={styles.DrDetailsSection}>            
            <div className={`${styles.Container} container`}>
                <div className={styles.TotalRecords}>{recordsCountTxt}</div>
                <TransitionGroup component="div" className='row'>
                    {listBody}
                </TransitionGroup>                         
                {loadMoreBtn}
            </div>
        </section>          
    );
};
export default DoctorsDetails;
