import React from 'react';
import styles from './DoctorDetails.module.scss';
const classNames = require('classnames');

const doctorDetails = ({ details }) => {
    const ico = icoClass => <div className={styles.Ico}><i className={icoClass}></i></div>

    const name = <div className={styles.Name}>{details['Name']}</div>;

    const servicesTypes = <div className={styles.ServicesTypes}>{
        details['Service Type'].map(item =>{
            return <div key={item}>{item}</div>
        })
    }</div>

    const gmapUrl = `https://www.google.com/maps/search/${encodeURIComponent(details['Address2'])}`;
    const address = <div className={styles.HasIcon}> 
            {ico('fas fa-map-marker-alt')} 
            <a href={gmapUrl} target="_blank" rel="noopener noreferrer">{details['Address1']} - {details['Address2']}</a>
        </div>

    const tel = <div className={styles.HasIcon}>
            {ico('fas fa-phone-alt')} 
            <div>{
                [details['Telephone1'], details['Telephone2']].map(item =>{
                    return item.length ? <a href={`tel:${item}`} key={item}>{item}</a> : null
                })
            }</div>        
        </div>

    const price = <div className={styles.HasIcon}>
            {ico('fas fa-dollar-sign')} 
        <div>{ details['Price'].length ? (details['Price'].replace(/,+$/,'')).replace(/,/g, ', ') : '/' }</div>
        
    </div>
    
    const daySeqs = [
        {'seq': details['Day Seq 1'], 'time':details['Day Seq 1 time']},
        {'seq': details['Day Seq 2'], 'time':details['Day Seq 2 time']},
        {'seq': details['Day Seq 3'], 'time':details['Day Seq 3 time']},
        {'seq': details['Day Seq 4'], 'time':details['Day Seq 4 time']}
    ];
    const seq = <div className={styles.Seq}>{
        daySeqs.map(({seq, time}) =>{
            if (seq && time){
                return <div className={styles.SeqItem} key={`${seq}-${time}`}><div className={styles.Key}>{seq} :</div><div className="time">{time}</div></div>
            }
            return null;
        })
    }</div>  
    
    return (
        <div className={classNames(styles.DoctorDetail, 'col-12', 'col-lg-6')}>
            <div className={styles.DoctorDetailInner}>
                {name}
                {servicesTypes}
                {address}
                {tel}
                {price}
                {seq}
            </div>            
        </div>  
    );
};
export default doctorDetails;
